<script>
export default {
  name: "nodeSettingComponent",
  components: {},
  data() {
    return {
      showDialog: false,
      isEdit: false,
      form: {
        id: "",
        parentfuncId: "",
        nSortIndex: "",
        sysId: "",
        sortIndex: 0,
        auth_YN: "",
        menuName: "",
        funcDesc: "",
        target: "",
        visible: true,
        url: "",
      },
      rules: {
        menuName: [{ required: false, message: "必填", trigger: "blur" }],
        target: [{ required: false, message: "必填", trigger: "blur" }],
        visible: [
          {
            required: false,
            message: "必填",
            trigger: "blur",
          },
        ],
        url: [{ required: false, message: "必填", trigger: "blur" }],
      },
      defaultProps: {
        children: "children",
        label(data) {
          return `${data.parent.menuName} - [可用狀態: ${data.parent.visible}]`;
        },
      },
      menu: [],
      sidebar: [],
    };
  },
  computed: {},
  methods: {
    targetHandler() {
      this.$refs.form.clearValidate("target");
    },
    async qryMenu() {
      const res = await this.$store.dispatch("qryMenu");
      const data = res.data.data;
      this.menu = data.menu.children;
      this.sidebar = data.sidebar.children;
    },
    async appendMenu(data) {
      data.visible = data.visible ? "Y" : "N";
      const res = await this.$store.dispatch("appendMenu", data);
      if (res.data.isOK) {
        this.$message({
          type: "success",
          duration: "5000",
          showClose: true,
          message: "新增成功",
        });
        this.showDialog = false;
        this.qryMenu();
      }
    },
    async editMenu(data) {
      data.visible = data.visible ? "Y" : "N";
      const res = await this.$store.dispatch("editMenu", data);
      if (res.data.isOK) {
        this.$message({
          type: "success",
          duration: "5000",
          showClose: true,
          message: "修改成功",
        });
        this.showDialog = false;
        this.qryMenu();
      }
    },
    // menu樹狀 render function
    renderContent(h, { node, data }) {
      return (
        <span class="custom-tree-node">
          <span>{node.label}</span>
          <span class="function-box">
            <el-button type="text" on-click={() => this.append(node, data)}>
              <i class="el-icon-document-add"></i>
            </el-button>
            <el-button
              class="ms-2"
              type="text"
              on-click={() => this.edit(node, data)}
            >
              <i class="el-icon-edit"></i>
            </el-button>
            <el-button
              type="text"
              slot="reference"
              class="ms-2"
              on-click={() => this.remove(node, data)}
            >
              <i class="el-icon-delete"></i>
            </el-button>
          </span>
        </span>
      );
    },
    append(node, data) {
      this.isEdit = false;
      this.showDialog = true;
      this.form = JSON.parse(JSON.stringify(data.parent));
      this.form.menuName = "";
      this.form.funcDesc = "";
      this.form.target = "";
      this.form.visible = true;
      this.form.url = "";
      this.form.parentfuncId = this.form.id;
    },
    edit(node, data) {
      this.form = JSON.parse(JSON.stringify(data.parent));
      this.isEdit = true;
      this.showDialog = true;
      this.form.menuName = data.parent.menuName;
      this.form.funcDesc = data.parent.funcDesc;
      this.form.target = data.parent.target;
      this.form.visible = this.form.visible === "Y" ? true : false;
      this.form.url = data.parent.url;
    },
    remove(node, data) {
      this.$confirm("確定要刪除嗎？", {
        confirmButtonText: "確定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(async () => {
          const res = await this.$store.dispatch("deleteMenu", data.parent);
          if (res.data.isOK) {
            this.$message({
              type: "success",
              duration: 5000,
              showClose: true,
              message: "刪除成功!",
            });
            this.qryMenu();
          }
        })
        .catch(() => {});
    },
    cancelDialog() {
      this.showDialog = false;
      this.$refs.form.clearValidate();
    },
    submitForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          // 兩個動作，新增&修改
          if (!this.isEdit) {
            this.appendMenu(this.form);
          } else {
            this.editMenu(this.form);
          }
        } else {
          this.$message({
            type: "error",
            duration: "5000",
            showClose: true,
            message: "資料填寫錯誤",
          });
        }
      });
    },
  },
  mounted() {
    this.qryMenu();
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;
  },
};
</script>

<template src="./nodeSetting.html"></template>
<style lang="sass" scoped src="./nodeSetting.sass"></style>
